<template>
    <div id='input'>
      <input v-model="message" placeholder="edit me">
      <p>Message is: {{ message }}</p>
      <ul id="example-1" >
        <li v-for="item in items" :key="item.id">
          {{ item.message }}
        </li>
      </ul>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'input',
  data () {
    return {
      message: '',
      items: [
        { message: 'Foo' },
        { message: 'Bar' }
      ]
    }
  }
}
</script>

<style scoped lang="stylus">

</style>



