<template>
  <div>
    <div>通用顶部</div>
    <div v-on:click="goBack()">返回</div>
    aaaaaaaaaaaaaaaa
    <div>通用底部</div>
  </div>
</template>

<script>
export default {
  name: 'demo1',
  methods: {
    goBack () {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push('/')
    }
  }
}

console.log(window.history)
</script>

<style scoped lang="stylus">

</style>



